import Vue from 'vue'
import Router from 'vue-router'
/* import login from '@/components/login'
import retrieve from '@/components/retrievePassword/retrievePassword.vue'
import changePassword from '@/components/changePassword/changePassword.vue'
import home from '@/components/homepage/home.vue' */

Vue.use(Router)


const router = new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: () => import('@/components/login')
    },
    {
      path: '/retrievePassword',
      name: 'retrieve',
      component: () => import('@/components/changePassword/changePassword.vue')
    },
    {
      path: '/changePassword',
      name: 'changePassword',
      component: () => import('@/components/retrievePassword/retrievePassword.vue')
    },
    {
      path: '/home',
      name: 'home',
      component: () => import('@/components/homepage/home.vue'),
      meta: { requiresAuth: true }
    }
  ]
})


router.beforeEach((to, from, next) => {
  console.log(to)
  if (to.meta.requiresAuth) {
   if(!localStorage.getItem('_pc_userName')) {
    const authUser = JSON.parse(window.localStorage.getItem('_pc_token'));
    if (authUser && authUser._pc_userName != '') {
      console.log("pd "+ authUser._pc_userName != '');
      next()
    } else {
      console.log("请先登录老铁~");
      next({name: 'login',params:{"msg":"请先登录老铁~"}})
    }
   } 
}else{
  next()
}
  
})


export default router


